/*
rem sizes:
40px = 2.5rem
30px = 1.875rem
25px = 1.5625rem
24px = 1.5rem
20px = 1.25rem
18px = 1.125rem
16px = 1rem
15px = .9375rem
14px = 0.875rem
13px = 0.8125rem
12px = 0.75rem
10px = 0.625rem
6px = 0.375rem
5px = 0.3125rem
4px = 0.25rem

1px = 0.0625rem */

html {
    box-sizing: border-box;
    height: 100%;
    color: #222;
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
}

*, *:before, *:after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

body {
    background-color: #fff;
    color: #fff;
    font-size: 0.875rem;
}

img {
	width: 100%;
    border: 1px solid #222;
}

h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0.625rem;
    font-family: 'Average', serif;
	font-weight: normal;
}

h2 {
    font-size: 3.75rem;
    margin: 20px 0;
    border-bottom: 1px solid #fff;
}

h3 {
    font-size: 1.5rem;
}

a {
    color: #ccc;
}

p {
    margin-bottom: 1.25rem;
    font-size: 0.875rem;
    line-height: 1.5rem;
}

ul {
    list-style: none;
}

li a {
    background: rgba(200,200,200, .3);
    border-bottom: 1px dotted #999;
}

pre {
    padding: 10px;
    background: #dedede;
    border-radius: 6px;
    font-size: 0.875rem;
}

.container {
    width: 80%;
    margin: 0 auto;
}

.grid {
    display: grid;
    display: -ms-grid;
    grid-gap: 1.25rem;
    width: 100%;
    margin: 0 auto 1.875rem;
    padding: 1.5625rem;
}

.grid > .grid {
    width: 100%;
    padding: 0;
}

article {
    position: relative;
}

/* columns */
.grid,
.grid[data-col="1"] {
    -ms-grid-columns: repeat(1, [col] 1fr);
	grid-template-columns: repeat(1, [col] 1fr);
}

[data-col="2"] {
	grid-template-columns: repeat(2, [col] 1fr);
    -ms-grid-columns: repeat(2, [col] 1fr);
}

[data-col="3"],
[data-col="1-2"],
[data-col="2-1"] {
    -ms-grid-columns: repeat(3, [col] 1fr);
	grid-template-columns: repeat(3, [col] 1fr);
}

[data-col="4"],
[data-col="1-3"],
[data-col="3-1"],
[data-col="2-1-1"],
[data-col="1-2-1"],
[data-col="1-1-2"] {
	grid-template-columns: repeat(4, [col] 1fr);
}

[data-col="5"],
[data-col="1-4"],
[data-col="4-1"],
[data-col="2-1-1-1"],
[data-col="1-2-1-1"],
[data-col="1-1-2-1"],
[data-col="1-1-1-2"] {
	grid-template-columns: repeat(5, [col] 1fr);
}

[data-col="6"],
[data-col="3-1-1-1"],
[data-col="1-3-1-1"],
[data-col="1-1-3-1"],
[data-col="1-1-1-3"] {
	grid-template-columns: repeat(6, [col] 1fr);
}

[data-col="7"] {
	grid-template-columns: repeat(7, [col] 1fr);
}

[data-col="8"] {
	grid-template-columns: repeat(8, [col] 1fr);
}

[data-col="4-1-1-1-1"],
[data-col="1-1-1-1-4"] {
    grid-template-columns: repeat(8, [col] 1fr);
}

/* twos */
[data-col="2-1"] > article:nth-child(1),
[data-col="2-1"] > div:nth-child(1) {
    grid-column: span 2;
}

[data-col="1-2"] > article:nth-child(2),
[data-col="1-2"] > div:nth-child(2) {
    grid-column: span 2;
}

[data-col="3-1"] > article:nth-child(1) {
    grid-column: span 3;
}

[data-col="1-3"] > article:nth-child(2),
[data-col="1-3"] > div:nth-child(2) {
    grid-column: span 3;
}

[data-col="4-1"] > article:nth-child(1) {
    grid-column: span 4;
}

[data-col="1-4"] > article:nth-child(2),
[data-col="1-4"] > div:nth-child(2) {
    grid-column: span 4;
}

/* threes */
[data-col="2-1-1"] > article:nth-child(1) {
    grid-column: span 2;
}

[data-col="1-2-1"] > article:nth-child(2) {
    grid-column: span 2;
}

[data-col="1-1-2"] > article:nth-child(3) {
    grid-column: span 2;
}

/* fours */
[data-col="2-1-1-1"] > article:nth-child(1) {
    grid-column: span 2;
}

[data-col="1-2-1-1"] > article:nth-child(2) {
    grid-column: span 2;
}

[data-col="1-1-2-1"] > article:nth-child(3) {
    grid-column: span 2;
}

[data-col="1-1-1-2"] > article:nth-child(4) {
    grid-column: span 2;
}

[data-col="3-1-1-1"] > article:nth-child(1) {
    grid-column: span 3;
}

[data-col="1-3-1-1"] > article:nth-child(2) {
    grid-column: span 3;
}

[data-col="1-1-3-1"] > article:nth-child(3) {
    grid-column: span 3;
}

[data-col="1-1-1-3"] > article:nth-child(4) {
    grid-column: span 3;
}

/* fives */
[data-col="4-1-1-1-1"] > article:nth-child(1) {
    grid-column: span 4;
}

[data-col="1-1-1-1-4"] > article:nth-child(5) {
    grid-column: span 4;
}


/* combinations */
[data-col="4x2"][data-layout="1-4-1"] {
    grid-template-columns: repeat(6, [col] 1fr);
}

[data-col="4x2"][data-layout="1-4-1"] article:nth-child(1) {
    grid-column: 1 / 3;
    grid-row: 1 / 3;
}

[data-col="4x2"][data-layout="1-4-1"] article:nth-child(2) {
    grid-column: 5 / 7;
    grid-row: 1 / 3;
}

[data-col="4x2"][data-layout="4-1-4"] {
    grid-template-columns: repeat(6, [col] 1fr);
}

[data-col="4x2"][data-layout="4-1-4"] article:nth-child(1) {
    grid-column: 3 / 5;
    grid-row: 1 / 3;
}


[data-col="3x2x1"] {
    grid-template-columns: repeat(6, [col] 1fr);
    grid-gap: 1.25rem;
}

[data-col="3x2x1"] article:nth-child(1)  {
    grid-column: span 3;
}

[data-col="3x2x1"] article:nth-child(2)  {
    grid-column: span 2;
}

[data-col="3x2x1"] article:nth-child(3)  {
    grid-column: span 1;
}

[data-col="3x2x1"] article:nth-child(4)  {
    grid-column: span 1;
}

[data-col="3x2x1"] article:nth-child(5)  {
    grid-column: span 3;
}

[data-col="3x2x1"] article:nth-child(6)  {
    grid-column: span 2;
}



[data-col="3x2x1"] [data-image-layout="square"]:after {
    padding-top: 50%;
}

[data-col="3x2x1"] .tile {
    border-radius: 0.375rem;
    box-shadow: 0 2px 8px rgba(0,0,0,.4);
}

/* end combinations */

figure .filler {
    display: inline-block;
    position: relative;
    width: 100%;
    max-height: 100%;
    padding-bottom: 56%;
    overflow: hidden;
    background: #222;
}

figure .filler > img {
    display: block;
    position: absolute;
    top: 25%;
    left: 50%;
    width: auto;
    max-width: 102%;
    height: 102%;
    max-height: 102%;
    -webkit-transform: translate(-50%, -25%);
    -ms-transform: translate(-50%, -25%);
    transform: translate(-50%, -25%);
    text-align: center;
}

figcaption {
    margin: 0.3125rem auto 0;
    color: #888;
    font-size: 0.75rem;
    font-weight: 300;
    line-height: 1rem;
}

/* media queries */
@media (min-width : 601px) and (max-width : 1024px) {
    [data-col="4"],
    [data-col="6"]  {
        grid-template-columns: repeat(2, [col] 1fr);
    }


    [data-col="4x2"][data-layout="1-4-1"] article:nth-child(1),
    [data-col="4x2"][data-layout="4-1-4"] article:nth-child(1) {
        grid-column: 1 / 1;
        grid-row: 1 / 1;
    }

    [data-col="4x2"][data-layout="1-4-1"] article:nth-child(2) {
        grid-column: 2 / 2;
        grid-row: 1 / 1;
    }
}

@media (max-width: 1024px) {
    [data-col="1-2"],
    [data-col="2-1"],
    [data-col="1-3"],
    [data-col="3-1"],
    [data-col="1-4"],
    [data-col="4-1"],
    [data-col="4-1-1-1-1"],
    [data-col="1-1-1-1-4"] {
        grid-template-columns: repeat(2, [col] 1fr);
    }

    [data-col="2-1"] > article:nth-child(1),
    [data-col="1-2"] > article:nth-child(2),
    [data-col="3-1"] > article:nth-child(1),
    [data-col="1-3"] > article:nth-child(2),
    [data-col="4-1"] > article:nth-child(1),
    [data-col="1-4"] > article:nth-child(2) {
        grid-column: span 1;
    }

    [data-col="4-1-1-1-1"] > article:nth-child(1) {
        grid-column: span 2;
    }

    [data-col="1-1-1-1-4"] > article:nth-child(5) {
        grid-column: span 2;
    }
}

@media (min-width : 480px) and (max-width : 768px) {
    [data-col="5"] {
        grid-template-columns: repeat(2, [col] 1fr);
    }

    [data-col="5"] > article:nth-child(1) {
        grid-column: span 2;
    }
}

@media (max-width: 768px) {
    [data-col="2-1-1"] > article:nth-child(1) {
        grid-column: span 4;
    }

    [data-col="2-1-1"] > article:nth-child(2),
    [data-col="2-1-1"] > article:nth-child(3) {
        grid-column: span 2;
    }

    [data-col="1-2-1"] > article:nth-child(2) {
        grid-column: col / span 4;
        order: 1;
    }

    [data-col="1-2-1"] > article:nth-child(1) {
        grid-column: span 2;
    }

    [data-col="1-2-1"] > article:nth-child(3) {
        grid-column: span 2;
    }

    [data-col="1-1-2"] > article:nth-child(1),
    [data-col="1-1-2"] > article:nth-child(2) {
        grid-column: span 2;
    }

    [data-col="1-1-2"] > article:nth-child(3) {
        grid-column: span 4;
    }
}

@media (max-width: 600px) {
    .container {
        width: 100%;
        padding: 0;
    }

    .grid[data-margin="0"] {
        width: 100%;
        padding: 0;
    }

    [data-col="2"],
    [data-col="3"],
    [data-col="4"],
    [data-col="6"],
    [data-col="7"],
    [data-col="8"],
    [data-col="4x2"][data-layout="1-4-1"],
    [data-col="4x2"][data-layout="4-1-4"] {
        grid-template-columns: repeat(1, [col] 1fr);
    }

    [data-col="4x2"][data-layout="1-4-1"] article:nth-child(1),
    [data-col="4x2"][data-layout="1-4-1"] article:nth-child(2),
    [data-col="4x2"][data-layout="4-1-4"] article:nth-child(1) {
        grid-column: auto;
        grid-row: auto;
    }
}

@media (max-width: 480px) {
    [data-col="5"],
    [data-col="2-1-1"],
    [data-col="1-2-1"],
    [data-col="1-1-2"],
    [data-col="4-1-1-1-1"],
    [data-col="1-1-1-1-4"] {
        grid-template-columns: repeat(1, [col] 1fr);
    }

    [data-col="5"] > article:nth-child(1),
    [data-col="2-1-1"] > article:nth-child(1),
    [data-col="2-1-1"] > article:nth-child(2),
    [data-col="2-1-1"] > article:nth-child(3),
    [data-col="1-2-1"] > article:nth-child(1),
    [data-col="1-2-1"] > article:nth-child(2),
    [data-col="1-2-1"] > article:nth-child(3),
    [data-col="1-1-2"] > article:nth-child(1),
    [data-col="1-1-2"] > article:nth-child(2),
    [data-col="1-1-2"] > article:nth-child(3),
    [data-col="4-1-1-1-1"] > article:nth-child(1),
    [data-col="1-1-1-1-4"] > article:nth-child(5) {
        grid-column: span 1;
    }
}